import React from 'react';
import PropTypes from 'prop-types';
import '../../styles/animate.min.css';
import '../../styles/slick.min.css';
//import '../../styles/slick-theme.min.css';
import Slider from "react-slick";
import _ from 'lodash';

export default class FinancialPublication extends React.Component {
    constructor() {
        super();
        this.state = {
            currentYear: 0
        }
    }
    static propTypes = {
        items: PropTypes.object.isRequired
    };

    selectYear (key){
        this.setState({
            currentYear: key
        });
    };

    render() {
        var settings = {
            dots: false,
            infinite: false,
            speed: 500,
            slidesToShow: 5,
            slidesToScroll: 5
        };
        let years = _.keys(this.props.items).sort().reverse();
        return (
            <div>
                <Slider {...settings}>
                    <div key="0" >
                        <button onClick={() => this.selectYear(0)}>All</button>
                    </div>
                    {years.map(year => (
                        <div key={year}>
                            <button onClick={() => this.selectYear(year)}>{year}</button>
                        </div>
                    ))}
                </Slider>
                <div>
                    {years.map(year => (
                        <div key={year}>
                            {this.state.currentYear === year || this.state.currentYear === 0 ? (
                                <div className={'animated fast bounceInUp'}>
                                    <h3>{year}</h3>
                                    <div>
                                        <ItemPeriod items={this.props.items[year]}/>
                                    </div>
                                </div>
                            ) : null}
                        </div>
                    ))}
                </div>
            </div>
        )
    }
}

class ItemPeriod extends React.Component {

    static propTypes = {
        items: PropTypes.object.isRequired
    };

    render() {
        let periods = Object.keys(this.props.items);
        return (
            <>
            {periods.map(period => (
            <div key={period}>
                <h4>{period}</h4>
                <div>
                    <div>
                        {this.props.items[period].map(node => (
                            <div key={node.nid}>
                                <a href={node.file} target="_blank">
                                    <h5>{node.title}</h5><span>{node.date}</span>
                                </a>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
            ))}
        </>
        );
    }
}